import React from "react";
import { useLocation, useNavigate } from "react-router-dom";
// import { Link } from "react-router-dom";

const RegisterAccept= () => {
  const location = useLocation();
  let navigate = useNavigate();
  const handelerBackNavigate = () => { 

    navigate(-1)
   }
  return (
    <div className="main-login">
      <div className="box-icon-login">
        <i className="fa-solid fa-angle-left" onClick={handelerBackNavigate}></i>
      </div>

      <h1 className="title-login"> ثبت نام شما با موفقیت انجام شد</h1>
      <h6 className="title-little-login">
      نام کاربری و گذارواژه به شماره همراه  {location.state.PhoneNumber}
ارسال می‌شود
      </h6>
      <div className="box-form-login">
        <button className="btn-submit ">ورود به اپلیکیشن</button>
      </div>
    </div>
  );
};

export default RegisterAccept;
