import React from "react";
import Topbar from "../../components/topbar";

const SettingPeriodicServices = () => {
  return (
    <div className="setting-main">
      <Topbar name="  یادآوری سرویس های دوره ایی  " />
      <div className="container-setting-add-box">
        <button className="setting-add-box">
          <i className="fa-solid fa-plus"></i>
        </button>
      </div>
      <div className="setting-option">
        <button className="setting-option-button-delete">
          <div>
            <span>  تعویض روغن </span>
          </div>
          <i className="fa-solid fa-trash-can"></i>
        </button>
      </div>
    </div>
  );
};

export default SettingPeriodicServices;
