import React from 'react'
import img404 from "../asset/image/404.png"
import { useNavigate } from 'react-router-dom';

const NotFound = () => {

  const navigate = useNavigate();

  const hnadelerCliCk = () => { 
    navigate(-1)
   }
  return (
    <div className="main-notFound">
        <img className="img-notFound" src={img404} alt="img404" />
        <h1 className="title-notFound">صفحه مورد نظر پیدا نشد !</h1>
        <button className=" btn-submit btn-notFound"  onClick={hnadelerCliCk}>
          بازگشت به صفحه قبل
        </button>
    </div>
  )
}

export default NotFound