import React, { useState } from "react";
import Topbar from "../../components/topbar";
import DatePicker from "react-multi-date-picker";
import persian_fa from "react-date-object/locales/persian_fa";
import persian from "react-date-object/calendars/persian";
import InputIcon from "react-multi-date-picker/components/input_icon";
import weekends from "react-multi-date-picker/plugins/highlight_weekends";
import TimePicker from "react-multi-date-picker/plugins/analog_time_picker";
import "react-multi-date-picker/styles/backgrounds/bg-dark.css";
import "react-multi-date-picker/styles/colors/yellow.css";
import "react-multi-date-picker/styles/colors/analog_time_picker_yellow.css";
import imgReportFuelConsumption from "../../asset/image/fuel-pump.png"
const ReportFuelConsumption = () => {
  const [value, setValue] = useState(new Date());

  return (
    <div className="setting-main main-reports">
      <Topbar name=" مصرف سوخت " />
      <div className="filter-topbar">
        <div className="box-row-input-black_low">
          <div className="container-input-black_low_tow_item ">
            <label htmlFor="startTime-form">از تاریخ: </label>
            <div style={{ direction: "rtl" }}>
              <DatePicker
                value={value}
                onChange={setValue}
                className="bg-dark yellow"
                plugins={[<TimePicker position="left" />, weekends()]}
                calendar={persian}
                locale={persian_fa}
                calendarPosition="bottom-right"
                inputClass="input-black_low"
                containerStyle={{
                  width: "100%",
                }}
              />
              <span class="icon-datapicker"></span>
            </div>
          </div>
          <div className="container-input-black_low_tow_item ">
            <label htmlFor="endTime-form">تا تاریخ: </label>
            <div style={{ direction: "rtl" }}>
              <DatePicker
                value={value}
                onChange={setValue}
                className="bg-dark yellow"
                plugins={[<TimePicker position="left" />, weekends()]}
                calendar={persian}
                locale={persian_fa}
                calendarPosition="bottom-right"
                inputClass="input-black_low"
                containerStyle={{
                  width: "100%",
                }}
              />
              <span class="icon-datapicker"></span>
            </div>
          </div>
        </div>

      </div>
      <div className="container-reportFuelConsumption">
          <div className="box-reportFuelConsumption">
            <img src={imgReportFuelConsumption} alt={"reportFuelConsumption"} className="img-reportFuelConsumption"/>
          </div>
          <div className="box-reportFuelConsumption">
                <p>
                  مصرف سوخت شما :
                </p>
                <h1>
                  1555556
                </h1>
                <p>
                  لیتر می باشد
                </p>
          </div>
        </div>
    </div>
  );
};

export default ReportFuelConsumption;
