import React, { useEffect, useState } from "react";
import Topbar from "../../components/topbar";
import LoadingStep from "../loadingStep";

const SettingTrackerManagement = () => {
  const [showModal, setShowModal] = useState(false);
  const [styleWidth, setStyleWidth] = useState({
    width: "0",
  });
  const toggleModal = () => {
    setShowModal(!showModal);
  };
  useEffect(() => {
    let width = document.getElementsByTagName("main")[0].offsetWidth;
    setStyleWidth({
      width: width,
    });
  }, []);

  const Modal = ({ children }) => (
    <div className="modal-option" onClick={toggleModal}>
      {children}
    </div>
  );

  const Component = ({ toggleModalClose }) => (
    <div
      className="compoenent-modal-option"
      style={styleWidth}
      onClick={(e) => {
        // do not close modal if anything inside modal content is clicked
        e.stopPropagation();
      }}
    >
      <div className="modal-option-topbar">
        <h1>افزودن ردیاب </h1>
        <i onClick={() => toggleModalClose()} class="fa-solid fa-xmark"></i>
      </div>
      <div className="container-input-black_low_one_item">
        <label htmlFor="sim-form">شماره سیمکارت داخل ردیاب: </label>
        <input
          id="sim-form"
          className="input-black_low"
          type="text"
          placeholder="شماره سیمکارت داخل ردیاب"
        />
      </div>
      <div className="container-input-black_low_one_item">
        <label htmlFor="phoneUser1">شماره مالک 1 : </label>
        <input
          id="phoneUser1"
          className="input-black_low"
          type="number"
          placeholder="شماره مالک 1"
        />
      </div>
      <div className="container-input-black_low_one_item">
        <label htmlFor="phoneUser2">شماره مالک 2 : </label>
        <input
          id="phoneUser2"
          className="input-black_low"
          type="number"
          placeholder="شماره مالک 2"
        />
      </div>
      <div className="container-input-black_low_one_item">
        <label htmlFor="phoneUser3">شماره مالک 3 : </label>
        <input
          id="phoneUser3"
          className="input-black_low"
          type="number"
          placeholder="شماره مالک 3"
        />
      </div>
      {/* <div className="container-input-black_low_one_item box-phonenumber-user-settingTrackerManagement">
        <label htmlFor="phoneUser-form">شماره مالک :</label>
        <div className="phonenumber-user-settingTrackerManagement">
          <input
            id="phoneUser-form"
            className="input-black_low"
            type="text"
            placeholder="شماره مالک"
          />
          <div className="btn-add-phonenumber-user">
            <i className="fa-solid fa-plus"></i>
          </div>
        </div>
      </div> */}
      <div className="box-row-input-black_low">
        <div className="container-input-black_low_tow_item ">
          <label htmlFor="sim-form">اسم خودرو </label>
          <input
            list="brow"
            className="input-black_low"
            placeholder="اسم خودرو  "
          />
          <datalist id="brow">
            <option value="پراید" />
            <option value="سمند" />
            <option value="206" />
            <option value="پرشیا" />
            <option value="x33" />
          </datalist>
        </div>
        <div className="container-input-black_low_tow_item ">
          <label htmlFor="sim-form">رنگ خودرو </label>
          <input
            list="brow"
            className="input-black_low"
            placeholder=" رنگ خودرو "
          />
          <datalist id="brow">
            <option value="پراید" />
            <option value="سمند" />
            <option value="206" />
            <option value="پرشیا" />
            <option value="x33" />
          </datalist>
        </div>
      </div>
      <div className="box-row-input-black_low">
        <div className="container-input-black_low_tow_item ">
          <button className="btn-modal-option-cancel">لغو</button>
        </div>
        <div className="container-input-black_low_tow_item">
          <button className="btn-modal-option-ok">تایید</button>
        </div>
      </div>
    </div>
  );

  return (
    <>
    <LoadingStep/>
      <div className="setting-main">
        <Topbar name="مدیریت ردیاب‌ها" />
        {showModal && (
          <Modal>
            <Component toggleModalClose={toggleModal} />
          </Modal>
        )}
        <div className="container-setting-add-box">
          <button className="setting-add-box" onClick={() => toggleModal(true)}>
            <i className="fa-solid fa-plus"></i>
          </button>
        </div>
        <div className="setting-option">
          <button className="setting-option-button">
            <div>
              <span>سمند سفید</span> | <span>علی خالقی فر</span>
            </div>
            <span className="icon-edit-and-view"></span>
          </button>
          <button className="setting-option-button">
            <div>
              <span>سمند سفید</span> | <span>علی خالقی فر</span>
            </div>
            <span className="icon-edit-and-view"></span>
          </button>
        </div>
      </div>
    </>
  );
};

export default SettingTrackerManagement;
