import React, { useState } from "react";
import Topbar from "../../components/topbar";

const RemoteAllergy = () => {
  const [valueRange, setValueRange] = useState(5);
  const handelerRange = (e) => {
    setValueRange(e.target.value);
  };
  return (
    <div className="setting-main">
      <Topbar name=" تنظیم  حساسیت شوک" />
      <div className="maxSpeed">
        <p style={{ color: "#B28228" }}>
          تنظیم حساسیت شوک : <span>{valueRange}</span>
        </p>
        <input
          id="range"
          type="range"
          min={1}
          max={50}
          defaultValue={5}
          onChange={handelerRange}
        />
        <div className="range-number">
          <soan>0</soan>
          <soan>50</soan>
        </div>
        <div className="box-btn-setting-change-password">
          <button className="setting-btn-cancel">لغو</button>
          <button className="setting-btn-ok">تایید</button>
        </div>
      </div>

    </div>
  );
};

export default RemoteAllergy;
