import React from "react";
import Topbar from "../../components/topbar";

const SettingChangePassword = () => {
  return (
    <div className="setting-main">
      <Topbar name="تغییر گذرواژه" />
      <div className="setting-change-password">
        <div className="setting-change-password-box">
          <label for="password_old">گذرواژه قبلی : </label>
          <input
            className="input-setting"
            type="password"
            id="password_old"
            name="password_old"
          />
        </div>
        <div className="setting-change-password-box">
          <label for="password_new">گذرواژه جدید : </label>
          <input
            className="input-setting"
            type="password"
            id="password_new"
            name="password_new"
          />
        </div>
        <div className="setting-change-password-box">
          <label for="password_new_confrim">تکرار گذرواژه جدید : </label>
          <input
            className="input-setting"
            type="password"
            id="password_new_confrim"
            name="password_new_confrim"
          />
        </div>
        <div className="box-btn-setting-change-password">
          <button className="setting-btn-cancel">لغو</button>
          <button className="setting-btn-ok">تایید</button>
        </div>
      </div>
    </div>
  );
};

export default SettingChangePassword;
