import React from "react";
import Topbar from "../../components/topbar";

const SettingDevices = () => {
  return (
    <div className="setting-main">
      <Topbar name="   دستگاه های متصل " />
      <div className="setting-option">
        <button className="setting-option-button-device">
          <div className="box-option-button-device">
            <div className="device-android"></div>
            <div className="box-option-button-device-text">
              <p>  اندروید </p>
              <p>  galexy j7 2016  </p>
              <p>  20 دقیقه پیش </p>
            </div>
          </div>
          <i className="fa-solid fa-trash-can"></i>
        </button>
        <button className="setting-option-button-device">
          <div className="box-option-button-device">
            <div className="device-ios"></div>
            <div className="box-option-button-device-text">
              <p>  اندروید </p>
              <p>  galexy j7 2016  </p>
              <p>  20 دقیقه پیش </p>
            </div>
          </div>
          <i className="fa-solid fa-trash-can"></i>
        </button>
        <button className="setting-option-button-device">
          <div className="box-option-button-device">
            <div className="device-web"></div>
            <div className="box-option-button-device-text">
              <p>  اندروید </p>
              <p>  galexy j7 2016  </p>
              <p>  20 دقیقه پیش </p>
            </div>
          </div>
          <i className="fa-solid fa-trash-can"></i>
        </button>
        <button className="setting-option-button-device">
          <div className="box-option-button-device">
                  حذف همه
          </div>
          <i className="fa-solid fa-trash-can"></i>
        </button>
      </div>
    </div>
  );
};

export default SettingDevices;
