import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
// import { Link } from "react-router-dom";
import axios from "axios";
const RegisterConfrim = () => {

  const [dataResult, setDataResult] = useState({});

  let navigate = useNavigate();
  const location = useLocation();
  // location.state.name
  useEffect(() => {
    axios
    .post(
        "http://87.248.153.185/PWA_API.php"
        ,
        {
          Action : "SendAuthSMSCode",
          IDUser : `+98${location.state.dataResult.PhoneNumber}`

        }

      )
      .then((res) => {

      })
      .catch((e) => {});

  }, []);

  const setDataProperty = (key, e) => {
    switch (key) {
      case "SMSCode":
        setDataResult({ ...dataResult, SMSCode: e.target.value });
        break;
    }
  };
  
const handelerClickSubmit = () => { 
  axios
  .post(
      "http://87.248.153.185/PWA_API.php"
      ,
      {
        Action : "Signup",
        IDUser : `+98${location.state.dataResult.PhoneNumber}`,
        SMSCode : dataResult.SMSCode,
        FirstName :location.state.dataResult.FirstName,
        LastName : location.state.dataResult.LastName,
        NationalCode :location.state.dataResult.NationalCode,
        CityID : location.state.idCity,
      }

    )
    .then((res) => {
      navigate("/registerAccept")
    })
    .catch((e) => {});

 }

 const handelerBackNavigate = () => { 

  navigate(-1)
 }

  return (
    <div className="main-login">
      <div className="box-icon-login">
        <i className="fa-solid fa-angle-left" onClick={handelerBackNavigate}></i>
      </div>

      <h1 className="title-login">کد تایید ارسال شده را وارد نمایید </h1>
      <h6 className="title-little-login">
        کد ارسال شده به شماره {`+98${location.state.dataResult.PhoneNumber}`} را وارد کنید
      </h6>
      <div className="box-form-login">
        <input
          type="text"
          className="input input-login"
          placeholder="کد تایید "
          onChange={(e) => {
            setDataProperty("SMSCode", e);
          }}
        />
        <h6 className="title-little-login"> کد ارسال نشد؟ </h6>
        <button className="btn-submit " onClick={handelerClickSubmit}>تایید</button>
      </div>
    </div>
  );
};

export default RegisterConfrim;
