import React from "react";

const MenuBottom = () => {
  return (
    <div className="MenuBottom">
      <div className="box-MenuBottom">
        <button className="option-MenuBottom">
          <span id="option-MenuBottom-setting"></span>
          تنظیمات
        </button>
        <button className="option-MenuBottom">
          <span id="option-MenuBottom-location"></span>
          ردیابی
        </button>
        <button className="option-MenuBottom">
          <span id="option-MenuBottom-report"></span>
          گزارشات
        </button>
        <button className="option-MenuBottom">
          <span id="option-MenuBottom-remote"></span>
          ریموت
        </button>
      </div>
    </div>
  );
};

export default MenuBottom;
