import React, { useEffect, useState } from "react";
import UserAccounts from "../../components/userAccounts";
import carRemote from "../../asset/image/carRemote.svg";
import MenuBottom from "../../components/menuBottom";
import {toast} from "react-toastify";

const Remote = () => {
  const [showModal, setShowModal] = useState(false);
  const [styleWidth, setStyleWidth] = useState({
    width: "0"
  });
  const toggleModal = () => {
      setShowModal(!showModal)
    
  }; 
  useEffect(() => {
    
    let width =document.getElementsByTagName('main')[0].offsetWidth;
    setStyleWidth({
      width: width
    })
  },[]);

  
  const Modal = ({ children }) => <div className="modal-remote" onClick={toggleModal}>{children}</div>;


  const Component = ({toggleModalClose}) => (
    <div className="compoenent-modal-remote" style={styleWidth}         onClick={e => {
      // do not close modal if anything inside modal content is clicked
      e.stopPropagation();
    }}>


    
      <div className="modal-remote-topbar">
      <h1>منو‌های بیشتر</h1>
      <i onClick={()=>toggleModalClose()} class="fa-solid fa-xmark"></i>
      </div>
      <div className="modal-box-options-remote">
        <div className="modal-box-option-remote">
          <button className="modal-option-remote">
            <span id="modal-option-remote-ear"></span>
           شنود
          </button>
          <button className="modal-option-remote">
            <span id="modal-option-remote-status"></span>
            وضعیت کلی خودرو 
          </button>
        </div>
        <div className="modal-box-option-remote">
          <button className="modal-option-remote">
            <span id="modal-option-remote-position"></span>
            تنظیم محدودیت جغرافیایی
          </button>
          <button className="modal-option-remote">
            <span id="modal-option-remote-allergy"></span>
تنظیم حساسیت شوک
          </button>
        </div>
        <div className="modal-box-option-remote">
          <button className="modal-option-remote">
            <span id="modal-option-remote-maxSpeed"></span>
            تنظیم حداکثر سرعت مجاز
          </button>
          <button className="modal-option-remote">
            <span id="modal-option-remote-alarmType"></span>
            تنظیم نوع هشدارها
          </button>
        </div>
        <div className="modal-box-option-remote" id="modal-box-option-remote-last">
          <button className="modal-option-remote" id="modal-option-remote-last">
            <span id="modal-option-remote-lockchild"></span>
           قفل کودک
          </button>
        </div>
      </div>
    </div>
  );
  const handeler = () => { 
    toast.success("با موفقیت ثبت شد", {
      position: toast.POSITION.BOTTOM_CENTER
    });
    // toast(<Msg />,{
    //   position: toast.POSITION.BOTTOM_CENTER
    // }) 
   }
  
  return (
    <div className="main-remote" >
      <UserAccounts />
      {showModal && (
            <Modal>
              <Component toggleModalClose={toggleModal} />
            </Modal>
          )}
      <div className="box-middel-remote">
        <div onClick={()=>toggleModal(true)} className="App">
          <button className="btn-little-remote-left">
            <span></span>
            منو بیشتر
          </button>

        </div>

        <div className="box-car-remote">
          <img src={carRemote} className="car-remote" alt="car-remote" />
        </div>
        <button className="btn-little-remote-right">
          <i className="fa-solid fa-microphone"></i>
          فرمان صوتی
        </button>
      </div>
      <div className="box-options-remote">
        <div className="box-option-remote">
          <button className="option-remote" onClick={handeler}>
            <span id="option-remote-look"></span>
            قفل کردن
          </button>
          <button className="option-remote">
            <span id="option-remote-unlook"></span>
            باز کردن
          </button>
        </div>
        <div className="box-option-remote">
          <button className="option-remote">
            <span id="option-remote-alert-on"></span>
            فعال کردن هشدار
          </button>
          <button className="option-remote">
            <span id="option-remote-alert-off"></span>
            غیرفعال کردن هشدار
          </button>
        </div>
        <div className="box-option-remote">
          <button className="option-remote">
            <span id="option-remote-off"></span>
            خاموش کردن
          </button>
          <button className="option-remote">
            <span id="option-remote-on"></span>
            روشن کردن
          </button>
        </div>
        <div className="box-option-remote">
          <button className="option-remote">
            <span id="option-remote-trunk"></span>
            صندوق پران
          </button>
          <button className="option-remote">
            <span id="option-remote-find-car"></span>
            پیدا کردن خودرو
          </button>
        </div>
      </div>
      <MenuBottom />
    </div>
  );
};

export default Remote;
