import axios from "axios";
import React, { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import logo from "../asset/image/Logo.png";
import Loading from "../components/loading";

const Login = () => {
  const [dataResult, setDataResult] = useState({});

  let navigate = useNavigate();

  const Msg = ({ closeToast, toastProps }) => (
    <div>
      Lorem ipsum dolor {toastProps.position}
      <button>Retry</button>
      <button onClick={closeToast}>Close</button>
    </div>
  );

  const handelerClickSubmit = () => {
    axios
      .post("http://87.248.153.185/PWA_API.php", {
        Action: "SendAuthSMSCode",
        IDUser: `+98${dataResult.PhoneNumber}`,
      })
      .then((res) => {
        navigate("/loginConfrim", {
          state: { PhoneNumber: `+98${dataResult.PhoneNumber}` },
        });
      })
      .catch((e) => {});
  };

  const setDataProperty = (key, e) => {
    switch (key) {
      case "PhoneNumber":
        setDataResult({ ...dataResult, PhoneNumber: e.target.value });
        break;
    }
  };

  return (
    <>
      <div className="main-login">
        <div className="box-icon-login">
          <i
            className="fa-solid fa-phone"
            onClick={() => {
              navigate("/contactUs");
            }}
          ></i>
        </div>
        <div className="box-logo-login">
          <img src={logo} alt="logo" />
        </div>
        <h1 className="title-login">به WeFinder خوش آمدید</h1>
        <h6 className="title-little-login">لطفا وارد حساب خود شوید</h6>
        <div className="box-form-login">
          <input
            type="text"
            className="input input-login"
            placeholder="شماره همراه "
            onChange={(e) => {
              setDataProperty("PhoneNumber", e);
            }}
          />
          <Link to="/register" className="box-title-little-login">
            <h6 className="title-little-login">
              حساب کاربری ندارید؟
              <span className="title-little-login-to-register">
                {" "}
                ثبت نام کنید{" "}
              </span>
            </h6>
          </Link>
          <button className="btn-submit" onClick={handelerClickSubmit}>
            تایید
          </button>
        </div>
      </div>
    </>
  );
};

export default Login;
