import React from 'react'
import Topbar from '../../components/topbar'

const RemoteStatusCar = () => {
  return (
    <div className="setting-main">
      <Topbar name="وضعیت کلی خودرو" />
      <div className="setting-option">
        <button className="setting-option-button">برق ردیاب : قطع </button>
        <button className="setting-option-button">درصد باتری ردیاب : 68 % </button>
        <button className="setting-option-button">دستگاه های متصل</button>
        <button className="setting-option-button">  وضعیت سوییچ : روشن</button>
        <button className="setting-option-button">وضعیت درب ها : بسته</button>
        <button className="setting-option-button">  وضعیت هشدارها : فعال</button>
      </div>
    </div>
  )
}

export default RemoteStatusCar