import React from "react";
import { Link } from "react-router-dom";
// import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
const ForgetPasswordConfrim = () => {
  return (
    <div className="main-login">
      <Link to="/" className="box-icon-back">
        <i className="fa-solid fa-chevron-left icon-back"></i>
      </Link>
      <h1 className="title-login">کد تایید ارسال شده را وارد کنید</h1>
      <div className="box-form-login">
        <input
          type="text"
          className="input input-login"
          placeholder=" کد تایید "
        />

        <button className="btn-submit ">تایید</button>
      </div>
    </div>
  );
};

export default ForgetPasswordConfrim;
