import React from "react";
import Topbar from "../../components/topbar";

const RemoteMaxSpeed = () => {
  return (
    <div className="setting-main">
      <Topbar name=" تنظیم نوع هشدار" />
      <div className="maxSpeed">
        <p style={{color:"#B28228"}}>روش های مورد نظر برای دریافت هشدارها را انتخاب کنید :</p>

        <div className="box-container-checkbox">
          <label class="container-checkbox">
            SMS
            <input type="checkbox" checked="checked" />
            <span class="checkmark"></span>
          </label>
          <label class="container-checkbox">
            تماس
            <input type="checkbox" />
            <span class="checkmark"></span>
          </label>
        </div>
        <div className="box-btn-setting-change-password">
          <button className="setting-btn-cancel">لغو</button>
          <button className="setting-btn-ok">تایید</button>
        </div>
      </div>
    </div>
  );
};

export default RemoteMaxSpeed;
