import React from "react";
import MenuBottom from "../../components/menuBottom";
import UserAccounts from "../../components/userAccounts";

const Reports = () => {
  return (
    <div className="main-remote">
      <UserAccounts />
      <div className="main-reports">
        <div className="container-row-reports">
          <div className="box-item-reports input">
            <span id="item-report-get-alarms"></span>
            <p className="text-item-report">هشدار های دریافت شده</p>
          </div>
          <div className="box-item-reports input">
            <span id="item-report-distance"></span>
            <p className="text-item-report"> مرور مسیر های پیموده شده</p>
          </div>
        </div>
        <div className="container-row-reports">
          <div className="box-item-reports input">
            <span id="item-report-parking"></span>
            <p className="text-item-report">مشاهده نقاط توقف </p>
          </div>
          <div className="box-item-reports input">
            <span id="item-report-reportsAll"></span>
            <p className="text-item-report">گزارشات کلی </p>
          </div>
        </div>
        <div className="container-row-reports">
          <div className="box-item-reports input">
            <span id="item-report-trip"></span>
            <p className="text-item-report">مرور سفر ها  </p>
          </div>
          <div className="box-item-reports input">
            <span id="item-report-fuel"></span>
            <p className="text-item-report">مصرف سوخت </p>
          </div>
        </div>
      </div>
      <MenuBottom />
    </div>
  );
};

export default Reports;
