import React, { useEffect, useState } from "react";
import logo from "../asset/image/Logo.png";

const RunLoading = () => {
  const [textTitle, setTextTitle] = useState();
  useEffect(() => {
    const timeout = setTimeout(() => {
      setTextTitle(
        <div className="box-text-runLoading ">
        <div class=" btn-shine">
          {/* <span className="text-runLoading ">WEFINDER</span>
          <br /> */}
          <span className="text-runLoading">ردیاب</span>
          &nbsp;
          <span className="text-runLoading">ماهواره</span>
          &nbsp;
          <span className="text-runLoading">ای</span>
          &nbsp;
          <span className="text-runLoading" >خودرو</span>
        </div>
      </div>
      );
     }, 3000);
   },[]);
  
  return (
    <div className="runLoading-main">
      <div className="box-logo-runLoading">
        <img className="logo-runLoading" src={logo} alt="logo" />
      </div>
      
{textTitle}
    </div>
  );
};

export default RunLoading;
