import React from "react";
import Topbar from "../../components/topbar";

const RemoteAlermType = () => {
  return (
    <div className="setting-main">
      <Topbar name="   تنظیم نوع هشدار  " />
      <div className="maxSpeed">
        <p style={{ color: "#B28228" }}>
          روش های مورد نظر برای دریافت هشدارها را انتخاب کنید :
        </p>

        <div className="box-container-checkbox">
          <label className="container-checkbox">
            غیرفعال
            <input type="radio" checked name="radio" />
            <span className="checkmark-checkbox" />
          </label>
          <label className="container-checkbox">
            فعال
            <input type="radio" name="radio" />
            <span className="checkmark-checkbox" />
          </label>
        </div>
        <div className="box-btn-setting-change-password">
          <button className="setting-btn-cancel">لغو</button>
          <button className="setting-btn-ok">تایید</button>
        </div>
      </div>
    </div>
  );
};

export default RemoteAlermType;
